@import "./src/assets/scss/_mixins.scss";
              @import "./src/assets/scss/_variables.scss";

.configure {
  @include flexible(max-content);
  flex-direction: column;
  justify-items: right;
  gap: 15px;
  box-sizing: border-box;
  &__label {
    @include flexible(100%);
    box-sizing: border-box;
    border: 1px solid $borderColor;
    padding: 15px;
    border-radius: 5px;
    flex-direction: column;
    gap: 10px;
  }
  &__button {
    @include flexible(max-content);
    @include button;
    padding: 5px;
    border: 1px solid $borderColor;
    border-radius: 5px;
    align-self: flex-end;
  }
}
